<template>
	<div id="Jobs">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">
          <div class="active-div">
            <div class="tab-div" :class="{selected:active==1}" @click="changeActive(1)">我要求职</div>
            <div class="tab-div" :class="{selected:active==2}" @click="changeActive(2)">我要招聘</div>
          </div>
        </div>
        <div class="van-nav-bar__right">
          <div class="font_12" style="color: #c10804;margin-right: -5px;" @click="upLoadJob">快捷发布</div>
        </div>
      </div>
    </div>
    <div class="login_box">
      <van-list v-model="loading" :finished="finished" @load="getList">
        <div class="background_color_ffffff border_bottom_eeeeee" style="padding: 8px;height: 52px;" v-for="(item,index) in list" @click="toJobInfo(item)">
          <img v-if="item.user" :src="item.user.userimg||'static/img/img_tx_zp.png'" style="height: 52px;float: left;width: 52px"/>
          <div style="margin-left: 70px">
            <span>{{item.title}}</span>
            <span class="font_12 float_right color_8b8b8b">发布时间:{{item.addtime}}</span>
          </div>
          <div style="margin-top: 21px;margin-left: 70px">
            <span v-if="item.user" class="font_12 color_8b8b8b">{{item.user.username}}</span>
            <div class="color_c10804 float_right font_12" v-if="item.price!='面议'">{{item.price}}/月</div>
            <div class="color_c10804 float_right font_12" v-if="item.price=='面议'">{{item.price}}</div>
          </div>
        </div>
      </van-list>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'Jobs',
		data() {
			return {
        active:1,
        list:[],
        pageNum:1,
        loading: false,
        finished: false,
        type: 6,
			}
		},
    mounted() {
      this.getList();
    },
		methods:{
      getList(){
        this.loading = true;
        let $this = this;
        let obj = {
          type: this.type,
          pageNum: this.pageNum,
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/getsamecitylist", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            if(rpdata.data.data.data.length>0){
              $this.list = $this.list.concat(rpdata.data.data.data);
              $this.pageNum++;
              $this.loading = false;
            }else if(rpdata.data.data.data.length==0){
              $this.finished = true;
              $this.loading = false;
            }
            // $this.list = rpdata.data.data.data;
          }
        })
        .catch(function(err) {})
      },
		  changeActive(active){
		    this.active = active;
		    this.list = [];
		    this.pageNum = 1;
        this.finished = false;
		    if(this.active==1){
		      this.type = 6;
          this.getList();
        }else{
          this.type = 5;
          this.getList(5);
        }
      },
      toJobInfo(dataMap){
        if(this.active==2){
          this.$store.commit('setJobInfo', dataMap);
          this.$router.push('JobInfo');
        }else{
          this.$store.commit('setResumeInfo', dataMap);
          this.$router.push('resume');
        }
      },
      toJobApplyList(){
        this.$router.push('JobApplyList');
      },
      toJobList(){
        this.$router.push('JobList');
      },
      upLoadJob(){
        if(this.active==1){
          this.$router.push('addResume')
        }else{
          this.$router.push('addJob')
        }
      },
		}
	}
</script>
<style>
</style>
<style scoped>
  .active-div{
    border: 2px solid #c10804;
    height: 32px;
    line-height: 32px;
    margin-top: 6px;
    border-radius: 5px;overflow: hidden
  }
  .tab-div{
    display: inline-block;
    width: 50%;
    color: #c10804;
  }
  .selected{
    background-color: #c10804;
    color: #fff !important;
  }
  .login_box{
    margin-top: 50px;
  }
</style>



